header div{
  width: 100%;
  height: 100px;
}
footer div{
  width: 100%;
  height: 100px;
}
.main{
  width:70%;
  margin: 100px auto;
  height: 3000px;
  border-right: 1px solid rgb(117, 114, 114);
  border-left: 1px solid rgb(117, 114, 114);
  border-bottom: 1px solid rgb(117, 114, 114);
}
.wrapper1{
  display: grid;
  grid-template-columns: repeat(3,1fr);
  border-top: 1px solid rgb(117, 114, 114);
  grid-auto-rows: minmax(100px, auto);
}
.wrapper2{
  display: grid;
  grid-template-columns: 27% repeat(4,12%) 25%;
  border-top: 1px solid rgb(117, 114, 114);
  grid-auto-rows: minmax(100px, auto);
}
.wrapper3{
  display: grid;
  grid-template-columns: repeat(3,1fr);
  border-top: 1px solid rgb(117, 114, 114);
  grid-auto-rows: minmax(100px, auto);
  border-bottom: 1px solid rgb(117, 114, 114);
}
.row{
  display: contents;
}

.row div:not(:first-of-type) {
  border-left: solid 1px black;
}

.box1{
  position: relative;
}
.box1 img{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  width: 79%;
  height: 66%;
}
.box2{
  position: relative;
}
.box2 span{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  letter-spacing: 0.5em;
  font-family: 'Hiragino Mincho Pro';
  font-style: normal;
  font-weight: 250;
  font-size: 20px;
  text-align: center;
  width:100%
}
.box4{
  height: 400px;
  grid-column-start: 1;
  grid-column-end: 4;
  grid-row-start: 2;
  grid-row-end: 4;
  position: relative;
  border-top: 1px solid #000;
}
.box4 img{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  width: 40%;
  height: 90%;
}
.box8{
  position: relative;
  height: 280px;
  width: 100%;
}
.box8 span{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  letter-spacing: 0.5em;
  font-family: 'Hiragino Mincho Pro';
  font-style: normal;
  font-weight: 250;
  font-size: 26px;
  text-align: left;
  width:86%
}
.box9{
  position: relative;
}
.box9 span{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  writing-mode: vertical-rl;
  font-family: 'Hiragino Mincho Pro';
  font-style: normal;
  font-weight: 250;
  font-size: 20px;
  text-align: center;
}
